var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"experience-page",attrs:{"id":"experience"}},[_c('h1',{staticClass:"experience-page__title"},[_vm._v("выставка «опыт настоящего»")]),_c('div',{staticClass:"experience-page__wrapper first"},[_c('div',{staticClass:"experience-page__download-wrapper"},[_c('Gallery1')],1),_c('div',{staticClass:"experience-page__text first"},[_c('p',{domProps:{"innerHTML":_vm._s('Материалом для выставки послужили изображения, сохранившиеся на пленках фотоархива Василия Стахевича Мариничева (1926 г., д.&nbsp;Подрезово&nbsp;Семеновского уезда Нижегородской губернии — 2002 г. Петербург), многие годы работавшего в одном из&nbsp;институтов&nbsp;Ленинграда заведующим фотолабораторией. Из&nbsp;5000 изображений мы&nbsp;выбрали&nbsp;для&nbsp;проекта чуть менее двухсот, относящихся к&nbsp;1950-1960м годам. Предметом визуального исследования был досуг: время, предоставленное самому себе и тем людям, которые с тобой его разделяют.')}}),_c('p',{domProps:{"innerHTML":_vm._s('Фотографии из архива В.&nbsp;С.&nbsp;Мариничева, старые снимки 1950-х — начала 1960-х гг. Текст&nbsp;—&nbsp;Светлана Адоньева, куратор проекта.')}})])]),_c('GalleryBig'),_c('div',{staticClass:"experience-page__foto-block"},[_c('Gallery')],1),_c('div',{staticClass:"experience-page__quote"},[_vm._m(0),_c('div',{staticClass:"experience-page__quote-text",domProps:{"innerHTML":_vm._s('Качество свободного времени, как нам кажется, обеспечено величинами иного масштаба, нежели&nbsp;эпоха и социальная страта. Этот проект&nbsp;–&nbsp;попытка их делать различимыми')}}),_vm._m(1)]),_c('div',{staticClass:"experience-page__wrapper second"},[_c('div',{staticClass:"experience-page__text second"},[_c('p',{domProps:{"innerHTML":_vm._s(' Среди множества фотографий из семейных архивов, которые я&nbsp;попросила у своих друзей и&nbsp;коллег для работы над темой свадьбы, меня зацепили несколько из&nbsp;фотоархива семьи Мариничевых. Они не имели отношения к теме моего исследования, просто попались среди прочих отсканированных фотографий. Тридцать девять изображений. Что-то&nbsp;в&nbsp;них было захватывающее, на них хотелось смотреть. Разглядывание их&nbsp;приводило в дивное состояние&ndash;настроение.')}}),_c('p',{domProps:{"innerHTML":_vm._s('Этим состоянием-настроением хотелось делиться с другими: я&nbsp;показывала их&nbsp;на&nbsp;занятиях со&nbsp;студентами Школы документального кино и театра Марины Разбежкиной и Михаила Угарова. Время от&nbsp;времени я&nbsp;выкладывала некоторые из&nbsp;них&nbsp;на&nbsp;своей странице в&nbsp;фейсбуке: я&nbsp;возвращалась к&nbsp;этим фотографиям, когда мне хотелось перевести дыхание. В&nbsp;них не было ничего особенного.')}})]),_c('Gallery2')],1),_c('div',{staticClass:"experience-page__foto"},[_c('div',{staticClass:"experience-page__foto-block small"},[_c('Gallery3')],1),_c('div',{staticClass:"experience-page__foto-block small"},[_c('Gallery4')],1),_c('div',{staticClass:"experience-page__foto-block small"},[_c('Gallery5')],1)]),_c('GalleryBig2'),_c('div',{staticClass:"experience-page__wrapper third"},[_c('Gallery6'),_c('div',{staticClass:"experience-page__text third"},[_c('p',{domProps:{"innerHTML":_vm._s('Мальчик с кроликом и щенком сидит на диване и улыбается. Сначала ты видишь мальчика, потом щенка и лишь после этого обнаруживаешь, что на руках у&nbsp;мальчика кролик. Молодые мужчины дурачатся перед фотоаппаратом: залезли на&nbsp;камень посреди лужи вместе с двумя мальчишками и&nbsp;принимают позы. Полуголый малыш несет ягодины на блюдце, девочка постарше присела на корточки и смотрит куда-то сквозь него, а на заднем плане на них обоих смотрит женщина, остановившись с&nbsp;ведром в руках. Мальчишки идут по железнодорожным рельсам, раскинув руки для равновесия: двое в тюбетейках, один в&nbsp;фуражке. На залитой солнцем деревенской улице две женщины и о чем-то разговаривают, одну из&nbsp;них перекрывает фигура проходящего мимо них бородатого мужика.')}}),_c('p',{domProps:{"innerHTML":_vm._s('Три загорающие на одеялах женщины смотрят на что-то, чего мы не видим, а стоящий над ними мальчик смотрит на одну из них. Собака смотрит туда же, куда и&nbsp;мальчик. Собака на цепи, которая обмотана вокруг его&nbsp;голого торса. Два подростка в светлых рубашках, застегнутых на&nbsp;все пуговицы, позируют фотографу на фоне деревенского дома.')}})])],1),_c('div',{staticClass:"experience-page__quote"},[_vm._m(2),_c('div',{staticClass:"experience-page__quote-text",domProps:{"innerHTML":_vm._s('Рассматривая фотографию одну за&nbspдругой, я&nbsp;описываю мир, в котором все&nbsp;как&nbsp;-&nbsp;то очень ладно и&nbspхорошо устроено. Люди в этом мире<br />как дома, в которых живут: в них свет&nbspи&nbspтепло')}}),_vm._m(3)]),_c('div',{staticClass:"experience-page__foto m-margin"},[_c('div',{staticClass:"experience-page__foto-block small"},[_c('Gallery7')],1),_c('div',{staticClass:"experience-page__foto-block small"},[_c('Gallery8')],1),_c('div',{staticClass:"experience-page__foto-block small"},[_c('Gallery9')],1)]),_c('div',{staticClass:"experience-page__wrapper fourth"},[_c('div',{staticClass:"experience-page__text fourth"},[_c('p',{domProps:{"innerHTML":_vm._s('В них что-то совершается — размеренное и&nbsp;самодостаточное. А еще люди-дома основательно и&nbsp;уверенно размещены в&nbsp;пространстве. Оно им дается без труда, где&nbsp;бы они не находились, оно их надежно подхватывает.')}}),_c('p',{domProps:{"innerHTML":_vm._s('И не менее надежно они охвачены связью друг с другом, у&nbsp;них есть какие-то общие дела и&nbsp;общие основания жизни. И&nbsp;я&nbsp;понимаю, что&nbsp;качество жизненного мира, который открывается мне в этих фотографиях, невероятно высоко. И это не качество эпохи: о&nbsp;них иногда даже трудно судить (1950е) или места (Ленинград, поселок Токсово, деревня под Горьким). Качество открывающегося мира обеспечено взглядом фотографа: от него мир не прячется, не сжимается и&nbsp;не&nbsp;представляется. Под его взглядом он&nbsp;живет своей жизнью.')}})]),_c('Gallery10')],1),_c('div',{staticClass:"experience-page__foto-block-third"},[_c('div',{staticClass:"experience-page__foto-block-third big"},[_c('Gallery11')],1),_c('div',{staticClass:"experience-page__foto-block-third small m-margin"},[_c('Gallery12')],1)]),_c('GalleryBig3'),_c('div',{staticClass:"experience-page__wrapper fifth"},[_c('div',{staticClass:"experience-page__text fifth"},[_c('p',{domProps:{"innerHTML":_vm._s('Я знала, что автор этих фотографий Василий Стахевич Мариничев, воевал, служил в&nbsp;армии, занимался аэрофотосъемкой, что он родом из&nbsp;-&nbsp;под Горького, откуда еще до&nbsp;войны его отец и братья перебрались в Ленинград. Прошло пять лет, и нам удалось познакомиться со всеми фотопленками В. С. Мариничева, которые сохранились. Около тысячи изображений.')}}),_c('p',{domProps:{"innerHTML":_vm._s('Скрытое в них послание не&nbsp;дает его объявить: оно тут же превращается во что-то очень банальное, но послание в этих фотографиях точно есть. Может быть так: качество мира, его&nbsp;равновесность, самодостаточность и красота обеспечиваются усилием человека, который в нем обитает и его поддерживает.')}}),_c('p',{domProps:{"innerHTML":_vm._s('Дар удерживать мир в&nbsp;равновесии и полноте чем-то обеспечен: ты&nbsp;позволяешь миру быть ровно настолько, насколько ты позволяешь быть самому себе. Почувствуем разницу: быть или&nbsp;быть кем-то — мужчиной, девушкой, инженером, красавицей, врачом, ветераном войны. Любая номинация после «ты есть» или&nbsp;«я&nbsp;есть»&nbsp;выдергивает мир из&nbsp;настоящего, из пребывания в&nbsp;нем, и превращает в&nbsp;параметризованное пространство значений и&nbsp;положений.')}}),_c('p',{domProps:{"innerHTML":_vm._s('В узнавании всегда участвует что&nbsp;-&nbsp;то, что было в прошлом: мы&nbsp;опознаем только то, что&nbsp;различаем. А различаем мы только то, что научились различать:&nbsp;наш&nbsp;опыт&nbsp;не&nbsp;свободен. Он был как-то и кем-то задан: высказываниями и действиями других&nbsp;людей,&nbsp;которые&nbsp;совершали какие-то операции и тем самым обнаруживали вещи в их связях и&nbsp;отношениях. Я опознаю наблюдаемое мною лицо незнакомого человека как лицо ветерана войны, колхозника, пенсионера или мудреца, и,&nbsp;опознав, тут же оказываюсь в&nbsp;безвременье: теперь мы&nbsp;можем действовать только по ролям. Как колхозник и&nbsp;горожанин, мудрец и&nbsp;ищущий мудрости, ветеран и тот, кто уважает ветерана за его подвиг.')}})]),_c('div',{staticClass:"experience-page__foto fifth"},[_c('Gallery13')],1)]),_c('div',{staticClass:"experience-page__quote"},[_vm._m(4),_c('div',{staticClass:"experience-page__quote-text",domProps:{"innerHTML":_vm._s('Но для того, чтобы этот опыт&nbsp;был&nbsp;включен&nbsp;в&nbsp;твой мир и стал его фундаментом, нужны другие, которые случившийся с тобой мир разделят, другие, которые&nbsp;живут&nbsp;на&nbsp;тех&nbsp;же основаниях.')}}),_vm._m(5)]),_c('GalleryBig4'),_c('div',{staticClass:"experience-page__foto"},[_c('div',{staticClass:"experience-page__foto-block middle m-margin"},[_c('Gallery14')],1),_c('div',{staticClass:"experience-page__foto-block middle m-margin"},[_c('Gallery15')],1),_c('div',{staticClass:"experience-page__foto-block middle m-margin"},[_c('Gallery16')],1)]),_c('div',{staticClass:"experience-page__wrapper sixth"},[_c('Gallery17'),_c('div',{staticClass:"experience-page__text sixth"},[_c('p',{domProps:{"innerHTML":_vm._s('Откуда берется сила, позволяющая признать себя и&nbsp;других&nbsp;пребывающими в&nbsp;«здесь и&nbsp;сейчас»? Обнаружить себя и&nbsp;других не&nbsp;«ветеранами войны», «колхозниками» или&nbsp;же&nbsp;«школьницами», «начинающими клерками», «женщинами не первой молодости», не ролями, а&nbsp;людьми, разделяющими со&nbsp;мной настоящее. Я полагаю, что&nbsp;дар&nbsp;просто&nbsp;быть&nbsp;и&nbsp;признавать за&nbsp;миром и&nbsp;людьми право просто быть обеспечен знанием о краях жизни. Такое знание у многих из&nbsp;нас случается как опыт в&nbsp;ситуации близкой смерти или понесенной утраты, этот опыт застает тебя врасплох, в&nbsp;одиночестве, не&nbsp;готовым. ')}}),_c('p',{domProps:{"innerHTML":_vm._s('Советские родители редко оказывались теми людьми, которые подхватывали своих детей в&nbsp;этом опыте, чаще всего они сами не смогли его ни с кем разделить. За&nbsp;миром Василия Стахевича Мариничева, скрывается другой опыт и&nbsp;другое знание, разделенное и&nbsp;согласованное.')}}),_c('p',{domProps:{"innerHTML":_vm._s('Ты увидел нечто, понял, что это край, а&nbsp;кто&nbsp;-&nbsp;то старший встал рядом и сказал: «Да, то, что ты видишь — край. Теперь и&nbsp;ты знаешь, что у жизни есть края». Разделенное с другими знание о&nbsp;том, что у жизни есть край, дает силу смотреть в&nbsp;настоящее и&nbsp;видеть в нем другого в&nbsp;его&nbsp;полноте.')}})])],1),_c('div',{staticClass:"experience-page__foto"},[_c('div',{staticClass:"experience-page__foto-block middle"},[_c('Gallery18')],1),_c('div',{staticClass:"experience-page__foto-block middle"},[_c('Gallery19')],1),_c('div',{staticClass:"experience-page__foto-block middle"},[_c('Gallery20')],1)]),_c('div',{staticClass:"experience-page__big-foto mobile-wrapper"},[_c('GalleryBig5')],1),_c('div',{staticClass:"experience-page__wrapper seventh"},[_c('div',{staticClass:"experience-page__text fifth"},[_c('p',{domProps:{"innerHTML":_vm._s('Василий&nbsp;Стахеевич&nbsp;Мариничев, фотограф (1926 — 2002) родился в&nbsp;староверческом селе&nbsp;Подрезово Нижегородской губернии, в&nbsp;крестьянской семье, воевал, занимался&nbsp;аэрофотосъемкой, всю&nbsp;жизнь проработал на&nbsp;закрытом предприятии.')}}),_c('p',{domProps:{"innerHTML":_vm._s('Он был сыном Стахея Клементовича Мариничева (1906&nbsp;—&nbsp;1982). Стахей Клементович стал в 1922 году директором валяльной фабрики, которая до&nbsp;этого принадлежала их&nbsp;семье, его отец Клемент умер в 1917, наследником стал Стахей. В&nbsp;1927&nbsp;году он&nbsp;отписал свое имение младшему брату Афанасию и&nbsp;переселился в&nbsp;Ленинград. Жена его осталась в&nbsp;деревне, с&nbsp;детьми, позже он&nbsp;устроил их&nbsp;учиться и&nbsp;работать&nbsp;в&nbsp;Ленинграде, также как и&nbsp;Афанасия, который был «раскулачен» в начале 1930х годов. Стахей служил в&nbsp;армии, был старшим лейтенантом, имел награды и грамоты от органов советской власти.')}}),_c('p',{domProps:{"innerHTML":_vm._s('Но среди его документов есть один, который говорит о нем очень многое. Это рукописный помянник, где указаны «годины» и&nbsp;«помины» всего его рода — дядьев и бабушек, брата, умершего в блокаду, и матери. Эта бумага была у него под рукой, каждый день он знал, о&nbsp;ком и&nbsp;как молиться. Вряд ли о&nbsp;его молитвенном за всех предстоянии знали все его дети. Возможно и&nbsp;вообще никто об&nbsp;этом не знал. Но Василию Стахеевичу было откуда взять силу удерживать и&nbsp;различать глубину мира и&nbsp;собственную глубину.')}})]),_c('div',{staticClass:"experience-page__foto seventh"},[_c('Gallery21')],1)]),_c('GalleryBig6'),_c('div',{staticClass:"experience-page__foto-block-third margin-top"},[_c('div',{staticClass:"experience-page__foto-block-third big"},[_c('Gallery22')],1),_c('div',{staticClass:"experience-page__foto-block-third middle"},[_c('Gallery23')],1)]),_c('div',{staticClass:"experience-page__foto"},[_c('div',{staticClass:"experience-page__foto-block middle"},[_c('Gallery24')],1),_c('div',{staticClass:"experience-page__foto-block middle"},[_c('Gallery25')],1),_c('div',{staticClass:"experience-page__foto-block middle"},[_c('Gallery26')],1)]),_c('GalleryBig7'),_vm._m(6),_c('div',{staticClass:"experience-page__subscription-tablet"},[_c('Subscription',{directives:[{name:"show",rawName:"v-show",value:(_vm.subscriptionState == 'none'),expression:"subscriptionState == 'none'"}],on:{"subscriptionresult":_vm.subscriptionResult}}),_c('ModalSuccess',{directives:[{name:"show",rawName:"v-show",value:(_vm.subscriptionState == 'success'),expression:"subscriptionState == 'success'"}],on:{"close":_vm.closeModal}})],1)],1)}
